.tags {
  text-align: center;
}

.tags__synonyms {
  margin-top: 28px;
}

.tag {
  font-family: 'Neue Montreal', Helvetica, sans-serif;
  border: 0;
  margin: 1px 1px 1px 1px;
  padding: 0;
  border-radius: 24px;
  padding: 11px 20px 13px 20px;

  display: inline-block;
  font-size: 17px;
  line-height: 20px;
  background-color: #FFFFFF;

  transition: 200ms;
  cursor: pointer;

  &:hover {
    background-color: #FBFF00;
    color: #000000;
  }
}

.tag--synonyms {
  padding: 9px 16px 10px 16px;
  margin: 0 0 -5px 1px;

  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0.3px;
  background-color: #F3F5F6;

  display: inline-block;
}

.tag--active {
  background-color: #FBFF00;
  color: #000000;
}
