$color-link: #000000;

.link {
  text-decoration: none;
  color: $color-link;
  transition: 200ms;


  &:visited {
    opacity: .16;
  }

  &:hover {
    color: #C0EB75;
  }
}

.link--button {
  margin: 0 4px 0 4px;
  padding-top: 16px;
  padding-bottom: 12px;
  width: 96px;
  background-color: #F3F5F6;
  border-radius: 32px;

  &:hover {
    background-color: #FBFF00;
  }
}
