.buttons {
  width: 100%;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.button {
  border: 0;
  background: 0;
  font-size: 17px;

  transition: 200ms;
}

.button--primary {
  margin: 0 4px 0 4px;
  padding-top: 16px;
  padding-bottom: 12px;
  width: 96px;
  background-color: #F3F5F6;
  border-radius: 32px;

  &:hover {
    background-color: #FBFF00;
  }
}
