.image {

}

.image--facepalm {
  position: absolute;
  top: calc(50% - 196px);
  left: calc(50% - 188px);
  transform: scale(1.25);

  @media #{$mobile} {
    top: calc(50% - 176px);
    left: calc(50% - 172px);
    transform: scale(0.55);
  }
}

.image--margin-10t {
  margin-top: 10px;
}

.image--margin-64b {
  margin-bottom: 64px;
}

.image__logo {
  width: 100%;
}

.image__logo--bloomberg {
  width: 34%;
  padding-left: 2px;
  padding-bottom: 2px;

}

.image__logo--ft {
  width: 54%;
  padding-bottom: 4px;

}

.image__logo--wired {
  width: 30%;

}

.image--tinkoff {
  display: none;
}

.image--lavka {
  display: none;
}

.image--eats {
  display: none;
}

.image--cian {
  display: none;
}

.image--visible {
  display: block;
}
