.flashcard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  text-align: center;


  background-color: #FFFFFF;
  border-radius: 48px;
  padding: 36px 24px 16px 24px;
  margin-bottom: 4px;

}

.flashcard__top {
  width: 100%;
  cursor: pointer;
}

.flashcard__description {
  display: none;
}

.flashcard--expanded > .flashcard__description {
  animation: reveal 0.2s ease;
  display: block;
}

.flashcard--unexpanded > .flashcard__description {
  animation: unreveal 0.2s ease;
  display: none;
}

@keyframes reveal {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes unreveal {
    0% {
        opacity: 1;
        transform: translateY(0);
    }
    100% {
        opacity: 0;
        transform: translateY(-20px);
    }
}

.flashcard--show {
  display: flex;
}
