.text {
  font-size: 17px;
  line-height: 24px;

  margin:  0;
  padding: 0;
}

.text--heading {
  font-size: 44px;
  line-height: 44px;
  margin: 0;
  margin-bottom: 12px;
}

.text--serif {
  font-family: 'Wremena', serif;
  font-size: 18px;
  line-height: 22px;
}

.text--uppercase {
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1.85px;
  font-weight: 600;
}

.text--grey {
  color: #868E96;
}
