.grid {

  @media #{$tablet} {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 12px;
    row-gap: 8px;
  }

}

.grid--mobile {

  @media #{$mobile} {
    display: flex;
    justify-content: space-between;
  }

}

.grid__projects {
  margin-top: 138px;

  @media #{$mobile} {
    margin-top: 0;
  }
}

.grid--playground {
  grid-gap: 10px 16px;
}


.grid__links {
  display: flex;
}

.grid__col-1-2 {
  grid-column: 1 / 2;
}

.grid__col-1-3 {
  grid-column: 1 / 3;
}

.grid__col-1-4 {
  grid-column: 1 / 4;
}

.grid__col-1-5 {
  grid-column: 1 / 5;
}

.grid__col-1-6 {
  grid-column: 1 / 6;
}

.grid__col-1-7 {
  grid-column: 1 / 7;
}

.grid__col-2-3 {
  grid-column: 2 / 3;
}

.grid__col-2-4 {
  grid-column: 2 / 4;
}

.grid__col-2-5 {
  grid-column: 2 / 5;
}

.grid__col-2-6 {
  grid-column: 2 / 6;
}

.grid__col-2-7 {
  grid-column: 2 / 7;
}

.grid__col-3-4 {
  grid-column: 3 / 4;
}

.grid__col-3-5 {
  grid-column: 3 / 5;
}

.grid__col-3-6 {
  grid-column: 3 / 6;
}

.grid__col-3-7 {
  grid-column: 3 / 7;
}

.grid__col-4-5 {
  grid-column: 4 / 5;
}

.grid__col-4-6 {
  grid-column: 4 / 6;
}

.grid__col-4-7 {
  grid-column: 4 / 7;
}

.grid__col-5-6 {
  grid-column: 5 / 6;
}

.grid__col-5-7 {
  grid-column: 5 / 7;
}

.grid__col-6-7 {
  grid-column: 6 / 7;
}

.grid__margin--72b {
  margin-bottom: 72px;

  @media #{$mobile} {
   margin-bottom: 24px;
  }
}

.grid--footer {

  @media #{$mobile} {
    display: flex;
    justify-content: space-between;
  }
}

.grid--news {
  padding: 20px 30px;
}

.grid--borders {
  border-top: 1px solid #000000;
  padding-top: 12px;

  @media #{$mobile} {
    border-top: 0;
    padding-top: 0;
  }
}

.grid--desktop-visible {

  @media #{$mobile} {
    display: none;
  }
}

.grid--mobile-visible {

  @media #{$mobile} {
    display: block;
    margin-bottom: 24px;
  }

  @media #{$tablet} {
    display: none;
  }
}

.grid--next {

  @media #{$mobile} {
    padding: 0 12px;
  }
}
