@import 'normalize';
@import 'variables';
@import 'typography';
@import 'mixins';

@import 'blocks/text';
@import 'blocks/link';
@import 'blocks/image';

@import 'blocks/wrapper';
@import 'blocks/grid';
@import 'blocks/site-header';
@import 'blocks/margin';
@import 'blocks/button';
@import 'blocks/tag';
@import 'blocks/flashcard';

::selection {
  color: #FFFFFF;
  background-color: #FBFF00;
}


* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Neue Montreal', Helvetica, sans-serif;
  font-weight: 400;
  font-size: 30px;

  margin: 0;
  padding: 0;

  color: #000000;

  background: $color-background; //#A8A8A8;
  transition: all ease 700ms;
}

img {
  max-width: 100%;
  height: auto;
}

blockquote {
  margin: 0;
  padding: 0;
}

hr {
  display: none;
  margin: 0;
  opacity: .12;
}
